@-webkit-keyframes cloudmove {
  0% {
    transform: translateX(-450px);
  }
  100% {
    transform: translateX(1090px);
  }
}

@keyframes cloudmove {
  0% {
    transform: translateX(-450px);
  }
  100% {
    transform: translateX(1090px);
  }
}

.tstars {
  width: 100%;
}

.tstar {
  display: inline-block;
  position: relative;
  -webkit-animation: twinkle 7s -1s infinite;
          animation: twinkle 7s -1s infinite;
}

.tstar:nth-child(even) {
  -webkit-animation: twinkle 7s 2s infinite;
          animation: twinkle 7s 2s infinite;
}

@-webkit-keyframes twinkle {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.2;
  }
}

@keyframes twinkle {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.2;
  }
}

.tstar:before,
.tstar:after {
  content: "";
  display: block;
  width: 3px;
  height: 10px;
  background: #b0a69b;
  border-radius: 45%;
}

.tstar:before {
  transform: rotate(-45deg);
  position: absolute;
  top: 0;
}

.tstar:after {
  transform: rotate(45deg);
}

.tstar:nth-child(1) {
  transform: translate(60px, -210px);
}

.tstar:nth-child(2) {
  transform: translate(180px, -110px);
}

.tstar:nth-child(3) {
  transform: translate(210px, -215px);
}

.tstar:nth-child(4) {
  transform: translate(370px, -15px);
}

.tstar:nth-child(5) {
  transform: translate(440px, 50px);
}

.tstar:nth-child(6) {
  transform: translate(440px, -190px);
}

.tstar:nth-child(7) {
  transform: translate(-5px, 20px);
}

.mountain {
  width: 490px;
  height: 490px;
  margin: 10px auto 0;
  background: linear-gradient(-225deg, #796f74, #534a4f 50%, #4a4246 50%);
  border-radius: 60px;
  transform: rotate(-45deg);
  position: relative;
  z-index: -2;
}

.train-container {
  position: absolute;
  bottom: -30px;
}

.train {
  width: 1900px;
  -webkit-animation: move 8s -1.5s linear infinite forwards;
          animation: move 8s -1.5s linear infinite forwards;
}

@-webkit-keyframes move {
  0% {
    transform: translateX(600px);
  }
  100% {
    transform: translateX(-1450px);
  }
}

@keyframes move {
  0% {
    transform: translateX(600px);
  }
  100% {
    transform: translateX(-1450px);
  }
}

.train-car {
  display: inline-block;
  vertical-align: top;
  height: 70px;
  width: 240px;
  border: 1px solid #71656a;
  border-top: 2px solid #978a8e;
  background: #50464b;
  border-radius: 11px;
  padding-top: 18px;
  padding-left: 18px;
}

.head-car,
.end-car {
  width: 210px;
  border-left: 2px solid #978a8e;
  border-top-left-radius: 200px;
  padding-left: 25px;
}

.end-car {
  border-top-left-radius: 11px;
  border-top-right-radius: 200px;
  padding-left: 10px;
  padding-right: 25px;
}

.windows,
.door {
  display: inline-block;
}

.windows {
  width: 80px;
  height: 50px;
}

.head-car .windows,
.end-car .windows {
  width: 130px;
}

.w-left,
.w-right,
.head-car-window,
.end-car-window {
  display: inline-block;
  width: 37px;
  height: 26px;
  border-radius: 2px;
  background: #ffc72f;
  /*   vertical-align: top; */
}

.head-car-window,
.end-car-window {
  width: 25px;
  border-top-left-radius: 25px;
  margin-right: 15px;
  position: relative;
}

.end-car-window {
  border-top-left-radius: inherit;
  border-top-right-radius: 25px;
  margin-left: 15px;
  margin-right: 0;
}

.head-car-window:after,
.end-car-window:after {
  content: "";
  display: block;
  width: 5px;
  height: 26px;
  background: #554b50;

  position: absolute;
  right: -12px;
}

.end-car-window:after {
  left: -12px;
}

.door {
  background: #675e62;
  width: 25px;
  height: 50px;
  padding: 4px 5px 0;
  vertical-align: top;
  margin: 0 5px;
}

.end-car .door {
  margin: 0 6px;
}

.door:before {
  content: "";
  display: block;
  width: 15px;
  height: 20px;
  background: #53494f;
}

.train-track {
  position: relative;
  background: #43383e;
  width: 100%;
  height: 450px;
}

.ledge {
  height: 20px;
  width: 100%;
  /*   background: white; */
  border-bottom: 3px solid #392e34;
  margin-bottom: 40px;
}

.pillar {
  overflow-x: hidden;
  overflow-y: hidden;
}

.pillar > div {
  background: #534a4f;
  height: 120px;
  border-radius: 200px 200px 0 0;
  display: inline-block;
}

.pillar-left {
  width: 120px;
  margin-left: -60px;
  margin-right: 50px;
}

.pillar-middle,
.pillar-right {
  border-right: 5px solid #968b8e;
}

.pillar-middle {
  width: 160px;
  margin-right: 80px;
}

.pillar-right {
  width: 180px;
  border-right-width: 8px;
}

.desc {
  margin-bottom: 30px;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  color: #d3c5b2;
  line-height: 1.5;
}

.desc a,
.desc a:visited {
  font-weight: bold;
  color: #d3c5b2;
}

.scsizer {
  height: 100%;
  width: 100%;
  background-color: rgb(2, 5, 1);
}

.feather {
  position: absolute;
  height: 100px;
  width: 100px;
  top: 20px;
  left: 30px;
  background: url(/ambient-sound-mixer/static/media/pen.a9706d55.svg) no-repeat;
  background-size: cover;
  background-position-y: center;
  background-position-x: center;
  transform-origin: 55px 55px;
  -webkit-animation: scribble 0.25s linear infinite;
          animation: scribble 0.25s linear infinite;
}

.paper {
  position: absolute;
  height: 2px;
  width: 100px;
  background-color: whitesmoke;
  top: 120px;
  left: 25px;
}

